.edit_header {
    display: flex;
    text-align: right;
    border-bottom: 1px solid white;
    padding: 20px;
    justify-content: flex-end;
}
.edit_content {
    display: flex;
}

.edit_setting {
    max-width: 420px;
    float: right;
    margin-left: 150px;
    text-align: center;
}
.edit_container {
    display: flex;
    flex-direction: column;
    float: right;
    max-width: 420px;
    max-height: 550px;
    background-color: cadetblue;
    border-radius: 20px;
    overflow-y: auto;
    margin-bottom: 50px;
}
.edit_container::-webkit-scrollbar {
    display: none;
  }


.edit_container h1 {
    font-size: 70px;
    color: white;
    text-align: center;
    padding: 0px;
    margin-bottom: -10px;
    margin-top: -7px;
}

.edit_form {
    position: relative;
    text-align: center;

}

.edit_select {
    padding: 15px;
    width: 400px;
    border-radius: 40px;
    border: 2px solid;
    border-color:aqua;
    margin: 10px;
    outline: none;
    -webkit-appearance: none;
}

.edit_input {
    align-items: center;
    border-radius: 40px;
    padding: 15px;
    margin: 10px;
    border-color: aqua;
    width: 400px;
    outline: none;
}
.edit_input_file {
    border: 1px solid aqua;
    border-color: aqua;
    background-color: white;
    display: inline-block;
    padding: 15px;
    margin: 10px;
    border-radius: 40px;
    width: 400px;
    position: relative;
    text-align: left;
}

.edit_icon {
    margin-right: 20px;
    vertical-align: middle;
    color: grey
}
.edit_input_file p1 {
    color: black;
}
.edit_input_file input[type="file"] {
    height: 100%;
    left: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    width: 100%;
}


.edit_button {
    /* position: relative; */
    bottom: 5vh;
    color: white;
    height: 70px;
    /* max-height: 70px; */
    min-width: 250px;
    font-size: 25px;
    border-radius: 40px;
    background: linear-gradient(-135deg, #a1e9f2, #2fb6ab);
    border: none;
    transition: all 0.3s ease-in;
    position: relative;
    margin: 20px;
    /* margin-right: 300px; */
}

.edit_button:focus {
    outline: none;
}

.edit_button:hover {
    background: linear-gradient(-135deg, #83cfda, #4fe6d9);
}

.edit_button:active span::after {
    position: absolute;
    top: 0%;
    left: 0%;
    width: 300px;
    height: 90px;
    border-radius: 40px;
    margin-top: -10px;
    margin-left: -27px;
    content: "";
    background: linear-gradient(-135deg, #9acdf7, #6aeeff);
    border-radius: 100%;
    opacity: 0;
    animation: ripple 0.3s 1 ease-out;
}

.edit_select {
    padding: 15px;
    width: 400px;
    border-radius: 40px;
    border-color: aqua;
    margin: 10px;
    outline: none;
    -webkit-appearance: none;
}

.edit_time {
    padding: 7px;
    width: 400px;
    border-radius: 40px;
    margin: 10px;
    outline: none;
    background-color: white;
    border: aqua 1px solid;
}

.edit_input_time {
    padding: 5px;
    border: grey 1px solid;
    border-radius: 40px;
    margin-left: 10px;
    margin-right: 10px;
    outline: none;
}

.edit_form textarea {
    min-height: 200px
}




#profile_intro {
    word-wrap: break-word;
    white-space: pre-wrap;
}



