
.chatScreen {
    background-color: rgb(255, 218, 224);
    height: 100%;
    position: relative;
}

#chatScreen_logo {
    /* position: sticky; */
    position: absolute;
    color: antiquewhite;
    font-size: 200px;
    top: 350px;
    width: 100%;
}


.chatScreen_header {
    display: flex;
    justify-content: start;
    align-items: left;
    border-bottom: 1px solid none;
    background-color: rgb(255, 218, 224);
    height: 70px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 99;
}

#chatScreen_header_icon {
    padding: 20px;
    outline: none;
    margin: 10px;
  
  }

.chatScreen_messages {
    /* overflow-y: scroll;
    overflow-x: hidden;
    width: 100%; */
    min-height: 900px;
    padding: 20px;
    
}

.chatScreen_messages::-webkit-scrollbar {  /* Chrome, Safari 対応 */
    display:none;
}
.chatScreen_message {
    display: flex;
    padding: 10px;
}

.chatScreen_message small {
    font-size: 10px;
    color: white;
}

.chatScreen_image {
    width: 100%;

}

.chatScreen_reply {
    margin-left: 10px;
    background-color: white;
    padding: 15px;
    border-radius: 20px;

}


.chatScreen_sent {
    margin-left: auto;
    background-color: aqua;
    padding: 15px;
    border-radius: 20px;

}

.chatScreen_match {
    text-align: center;
    padding: 20px;
    color: grey;
    width: 100%;
}

.chatScreen_input {
    display: flex;
    padding: 5px;
    position: fixed;
    bottom: 0;
    width:100%;
    border-top: 1px solid none;
    background-color: rgb(255, 218, 224);
    margin-top:8px
}
.chatScreen_inputField {
    flex: 1;
    padding: 10px;
    border:none;
    outline: none;
    border-radius: 40px 0px 0px 40px / 40px 0px 0px 40px;
    margin-left: 10px;
}

.chatScreen_submit{
    border:none;
    margin-right: 20px;
    background-color: white;
    font-weight: bolder;
    color: palevioletred;
    border-radius: 0px 40px 40px 0px / 0px 40px 40px 0px;

}